/* === Base button === */

.toggle {
  position: relative;
  display: inline-block;
  width: 32px;
  height: 16px;

  label {
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    cursor: pointer;

    &::before {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      content: "";
      display: inline-block;
      width: 30px;
      height: 16px;
      background-color: var(--color-gray);
      border-radius: 16px;
      margin: auto;
      transition: background-color 0.3s ease;
    }

    &::after {
      position: absolute;
      top: 2px;
      right: 16px;
      bottom: 2px;
      content: "";
      width: 12px;
      height: 12px;
      background-color: #fff;
      border-radius: 50%;
      transition: right 0.3s ease;
      margin: auto;
    }

    &:hover::before {
      background-color: var(--color-gray-hover);
    }
  }

  input[type="checkbox"],
  input[type="radio"] {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;

    &:checked + label {
      &::before {
        background-color: currentColor;
      }

      &::after {
        right: 2px;
      }
    }

    &:focus + label::before {
      box-shadow: 0 0 0 2px color-mix(in srgb, currentColor 50%, transparent);
    }
  }

  input[type="checkbox"][disabled] + label,
  input[type="radio"][disabled] + label,
  &.toggle--disabled input[type="checkbox"] + label,
  &.toggle--disabled input[type="radio"] + label {
    pointer-events: none;

    &::before {
      background-color: white;
      box-shadow: 0 0 0 2px #DDDDDD;
    }

    &::after {
      background-color: #DDDDDD;
    }
  }

  &.toggle--small {
    width: 20px;
    height: 10px;
    border-radius: 10px;

    label {
      &::before {
        width: 20px;
        height: 10px;
      }

      &::after {
        width: 8px;
        height: 8px;
        top: 1px;
        right: 11px;
        bottom: 1px;
      }
    }

    input[type="checkbox"],
    input[type="radio"] {
      &:checked + label {
        &::after {
          right: 1px;
        }
      }
    }
  }
}
