/* === Base button === */

.button {
  all: unset;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  vertical-align: top;
  gap: 6px;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 100px;
  padding: 8px 20px;
  margin: 0;
  text-align: left;
  color: inherit;
  font: inherit;
  cursor: pointer;
  transition: 0.3s ease;

  &:focus {
    box-shadow: 0 0 0 2px color-mix(in srgb, currentColor 50%, transparent);
  }

  &[disabled],
  &.button--disabled {
    opacity: 0.5;
    pointer-events: none;
    cursor: default;
  }

  &.button--square {
    border-radius: 4px;
  }

  &.button--full-width {
    width: 100%;
    box-sizing: border-box;
  }

  &.button--large {
    font-size: 18px;
    padding: 12px 24px;
  }

  &.button--small {
    font-size: 14px;
    padding: 6px 18px;
  }
}
